.preloader {
	z-index: 2000;
	background-color: black;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	&__spin {
		position: absolute;
		width: 50px;
		height: 50px;
		border: 5px solid white;
		border-top: 5px solid black;
		border-radius: 50%;
		top: calc(50% - 25px);
		left: calc(50% - 25px);
		animation: 1s spin infinite linear; } }



@keyframes spin {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(360deg); } }

